import { Router } from "@angular/router";
import { Component, OnInit } from "@angular/core";
import { MatDialogRef } from "@angular/material/dialog";

@Component({
    selector: "app-shoplive",
    templateUrl: "./shoplive.component.html",
    styleUrls: ["./shoplive.component.scss"],
})
export class ShopliveComponent implements OnInit {
    constructor(
        public dialogRef: MatDialogRef<ShopliveComponent>,
        public _router: Router
    ) {}

    ngOnInit(): void {}

    gotoSellerPage() {
        this.dialogRef.close("Closing Message");
        this._router.navigate(["seller"]);
    }
}
